body {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f2d4d5;
  color: #48120c;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

.cooker {
  width: 32rem;
  height: 25rem;
  background-color: #525c58;
  border: 6px solid #000000;
  border-radius: 2rem;
  position: relative;
}

.cooker:after {
  position: absolute;
  content: "";
  width: 7rem;
  height: 2rem;
  background-color: #626d74;
  border: 6px solid #000000;
  left: -5rem;
  top: 3.5rem;
  z-index: -1;
  border-radius: 20px;
}

.cooker:before {
  position: absolute;
  content: "";
  width: 7rem;
  height: 2rem;
  background-color: #626d74;
  border: 6px solid #000000;
  right: -5rem;
  z-index: -1;
  top: 3.5rem;
  border-radius: 20px;
}

.lid {
  position: absolute;
  width: 100%;
  height: 2.5rem;
  background-color: #d4cbcb;
  border-top-left-radius: 1.5rem;
  border-top-right-radius: 1.5rem;
  border-bottom: 6px solid black;
}

.hidden {
  transform: translateY(-100%) translateX(-70%) rotate(-45deg);
  position: absolute;
}

.lid:before {
  position: absolute;
  content: "";
  width: 5.5rem;
  height: 3.5rem;
  border-radius: 1.5rem;
  top: -2.7rem;
  background-color: #dae4e0;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
  border: 6px solid black;
}

.butspace {
  left: 17px;
  position: absolute;
  display: table;
  margin: 5px;
  text-align: center;
  bottom: 10px;
}

.onn {
  right: 20px;
  bottom: 10px;
  width: 50px;
  height: 50px;
  margin-left: 10px;
  background: #009578;
  color: #ffffff;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 50%;
  border: none;
  text-decoration: none;
  transition: background 0.2s;
}

.onn:hover {
  background-color: blue;
}

#tr {
  size: 50px;
}
.screen {
  border: 2px solid black;
  border-radius: 5px;
  margin: 30px 30px 0px 30px;
  height: 130px;
  background-color: #4d4d00;
}
.power-on{
  background-color: #ffffcc;
}